
/* Flex Grid */

.container,
.md-container {
    display: flex;
    flex-wrap: wrap;

    .col-1,
    .col-md-1 {
        width: calc(100% / 12 * 1);
    }

    .col-2,
    .col-md-2 {
        width: calc(100% / 12 * 2);
    }

    .col-3,
    .col-md-3 {
        width: calc(100% / 12 * 3);
    }

    .col-4,
    .col-md-4 {
        width: calc(100% / 12 * 4);
    }

    .col-5,
    .col-md-5 {
        width: calc(100% / 12 * 5);
    }

    .col-6,
    .col-md-6 {
        width: calc(100% / 12 * 6);
    }

    .col-7,
    .col-md-7 {
        width: calc(100% / 12 * 7);
    }

    .col-8,
    .col-md-8 {
        width: calc(100% / 12 * 8);
    }

    .col-9,
    .col-md-9 {
        width: calc(100% / 12 * 9);
    }

    .col-10,
    .col-md-10 {
        width: calc(100% / 12 * 10);
    }

    .col-11,
    .col-md-11 {
        width: calc(100% / 12 * 11);
    }

    .col-12,
    .col-md-12 {
        width: calc(100% / 12 * 12);
    }
}

/* Media queries */

@media screen and (max-width: 699px) {
    .md-container {
        display: block;

        .col-md-1,
        .col-md-2,
        .col-md-3,
        .col-md-4,
        .col-md-5,
        .col-md-6,
        .col-md-7,
        .col-md-8,
        .col-md-9,
        .col-md-10,
        .col-md-11,
        .col-md-12 {
            width: 100%;
        }
    }
}
